    width: 100%;
    max-width: 490px;
    min-height: 605px;
    margin: auto;
    background-color: #00B2FF; }

.modal p {
  margin: 0 auto;
  padding: 0 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 140.62%;
  color: #FFFFFF; }

.accordeon-section__toggle-button {
  position: relative;
  display: none;
  width: 16px;
  height: 16px;
  font-size: 0;
  background-color: transparent;
  border: none;
  cursor: pointer; }
  .accordeon-section__toggle-button::before, .accordeon-section__toggle-button::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 100%;
    height: 1px;
    background-color: #C4C4C4;
    transition-property: background-color, opacity, transform;
    transition-duration: 0.15s;
    transition-timing-function: ease; }
  .accordeon-section__toggle-button::before {
    transform: translate(-50%, -50%); }
  .accordeon-section__toggle-button::after {
    transform: translate(-50%, -50%) rotate(90deg); }

.accordeon-section__toggle-button--tab-open::after {
  transform: translate(-50%, -50%); }

/*
*** Стили для интерактивности ***
*/
.accordeon-section__toggle-button:hover::before,
.accordeon-section__toggle-button:hover::after,
.accordeon-section__toggle-button:focus::before,
.accordeon-section__toggle-button:focus::after {
  background-color: #00B2FF; }

.accordeon-section__toggle-button:active::before,
.accordeon-section__toggle-button:active::after {
  opacity: 0.7; }

/*
==================================
--------- МЕДИАВЫРАЖЕНИЯ ---------
==================================
*/
@media (max-width: 767px) {
  .accordeon-section__toggle-button--js-active {
    display: block; }
  .accordeon-section .accordeon-section__tab--js-active {
    display: none; }
  .accordeon-section .accordeon-section__tab--tab-open {
    display: block; } }
